import React, { Component } from 'react';

import { MDBBtn, MDBInput } from "mdbreact";

import * as msg from '../../../const/message'
import '../../../styles/account.scss'
class EditPassword extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isUpdating: false,
            oldPass: '',
            newPass: '',
            confirmPass: ''

        }
    }

    changeHandler = event => {
        this.setState({
            [event.target.name]: event.target.value,
        });
    };
    submitHandler = event => {
        event.preventDefault();
        event.target.className += " was-validated";
        const { oldPass, newPass, confirmPass } = this.state
        const { changePassword } = this.props
        if( newPass !== confirmPass ) 
        {
            this.$utils.toastError(msg.MSG_PASSWORD_CONSISTENT)
            this.setState({ confirmPass: '' })
        }
        else if( newPass.length < 6 )
            this.$utils.toastError(msg.MSG_PASSWORD_SHORT)
        else changePassword({
            old_pass: oldPass,
            new_pass: newPass
        })
        
    }
    render() {
        const { oldPass, newPass, confirmPass } = this.state
        return (
            <form
                className='needs-validation'
                onSubmit={this.submitHandler}>
                <div className='row'>
                    <div className='row container'>
                        <div className='col-12'>
                            <MDBInput
                                outline
                                label="Mt khu c"
                                type="password"
                                name='oldPass'
                                onChange={this.changeHandler}
                                value={oldPass}
                                required
                            />
                        </div>
                        <div className='col-12 '>
                            <MDBInput
                                outline
                                label="Mt khu mi"
                                type="password"
                                onChange={this.changeHandler}
                                value={newPass}
                                name='newPass'
                                required
                            />
                        </div>
                        <div className='col-12 '>
                            <MDBInput
                                outline
                                label="Nhp li Mt khu mi"
                                type="password"
                                name='confirmPass'
                                onChange={this.changeHandler}
                                value={confirmPass}
                                required
                            />
                        </div>
                    </div>
                    <div className='col-6 container'>
                        <MDBBtn gradient='sunny-morning' type='submit'>Cp nht</MDBBtn>
                    </div>
                </div>
            </form>
        );
    }
}


export default EditPassword;